li{
    all: unseta;
}
.freecourselist__contents{
    display: flex;
    flex-direction: row;
    align-items: center;

   
    margin-bottom: 10px;

    width: 400px;

    cursor: pointer;
    padding: 0 10px;
}

.freecourselist__contents-img{
    width: 80px;
    height: 80px;
    border-radius: 5px;
    margin-right: 20px;
}

.freecourselist__texts{
    font-size: 14px;
    line-height: 150%;
    color: #141617;
}

.freecourselist__line{
    border: 0px;
    height: 1px;
    background: rgb(228, 235, 240);
}
